@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:regular,bold,italic&subset=latin,latin-ext);

* {
    font-family: 'Source Code Pro';
    font-size: 14px;
}
html, body {
    height: 100%;
    margin: 0;
}
h1{
    padding-top: 0px;
    margin-bottom: 3px;
}
.app {
      justify-content: center;
      margin: auto;
      max-width: 460px;

}
.bg {
    height:100%;
    margin: 0;
    background-image: url(/tftscout/static/media/petals.3c3988fa.png);
    background-size: 500px;
    padding-bottom: 100%;
    min-height: 100%;
}
.header {
    margin-top: 50px;
    margin-bottom: 20px;
    max-width: 430px;
}
.footer {
    max-width: 440px;
      position: fixed;
  color: white;
  text-align: center;
}
.teemo-img {
    transform: translate(0px, 15px);     
}
.PlayerTile {
    margin-bottom: 5px;
}
input {
    font-family: 'Source Code Pro';
    width: 95%;
    height: 95%;
    border: 0;
    background: transparent;
    padding-left: 10px;
    font-weight: bold;
    color: white;
}
input.green {
    background-color:#8eed90;
    color: white;
}
input.red {
    background-color: #ff5757;
    color: white;
}
input.grey {
    background-color: #c3c7c7;
    color: #d5dbd8;
}
input:hover {
    background: #eeeeee;
}
input:focus {
    outline: none;
    background: #737373;
}
